@layer reset {
  :where(:not(html, div, span, img, iframe, canvas, svg, video, audio):not(svg *, symbol *)) {
    all: unset;
    display: revert;
    outline: revert;
    list-style: none;
  }

  ::placeholder {
    color: unset;
    opacity: unset;
  }

  *, :before, :after {
    box-sizing: border-box;
  }

  :where(html) {
    -moz-text-size-adjust: none;
    text-size-adjust: none;
  }

  :where(a, button) {
    cursor: pointer;
  }

  :where(img, iframe, canvas, svg, video, audio) {
    max-block-size: 100%;
    max-inline-size: 100%;
    display: block;
  }

  :where(textarea) {
    white-space: pre-wrap;
  }

  :where(pre) {
    white-space: pre;
  }

  :where([hidden]) {
    display: none;
  }

  :where([contenteditable]:not([contenteditable="false"])) {
    -moz-user-modify: read-write;
    -webkit-user-modify: read-write;
    -webkit-user-select: auto;
    user-select: auto;
  }

  :where([draggable="true"]) {
    -webkit-user-drag: element;
  }

  :where(dialog) {
    width: fit-content;
    height: fit-content;
    margin: auto;
    position: absolute;
    inset-inline: 0;
  }

  :where(dialog:modal) {
    max-width: calc(100% - 6px - 2em);
    max-height: calc(100% - 6px - 2em);
    position: fixed;
    inset-block: 0;
  }

  ::-webkit-details-marker {
    display: none;
  }
}

@layer utility;

@layer typography {
  :root {
    --font-sans: system-ui, sans-serif;
    --font-slab: Rockwell, "Rockwell Nova", "Roboto Slab", "DejaVu Serif", "Sitka Small", serif;
    --font-serif: Charter, "Bitstream Charter", "Sitka Text", Cambria, serif;
    --font-mono: ui-monospace, "Cascadia Code", "Source Code Pro", Menlo, Consolas, "DejaVu Sans Mono", monospace;
  }

  * {
    line-height: calc(1em + .5rem);
  }

  body {
    font-family: var(--font-sans);
    color: var(--color-text-primary);
  }

  code, .monospace {
    font-family: var(--font-mono);
  }

  input, button, textarea, select {
    font: inherit;
    letter-spacing: inherit;
    word-spacing: inherit;
  }

  p, li, figcaption {
    text-wrap: pretty;
    max-width: 70ch;
  }

  .rainbow-text {
    background: linear-gradient(to right, var(--color-rainbow-red), var(--color-rainbow-orange), var(--color-rainbow-yellow), var(--color-rainbow-green), var(--color-rainbow-blue), var(--color-rainbow-purple));
    -webkit-text-fill-color: transparent;
    background-clip: text;
    width: max-content;
  }
}

@layer theme;
